import './App.css';
import { useRef, useCallback, useState, useEffect } from 'react';
import { createWorker } from 'tesseract.js';
import styled from 'styled-components';
import Webcam from 'react-webcam';
import { Box, Button, TextField, Typography } from '@mui/material';
import { setCommentRange } from 'typescript';
import axios, { AxiosResponse } from "axios";
import r from "../lib/googleApi.json"

type RES = typeof r

function App() {
  // useState 
  // 
  const [image, setImage] = useState<string | null | undefined>(null)
  // OCR 
  const [ocr, setOcr] = useState<string>('')
  // ISBN
  const [isbn, setIsbn] = useState<string>('')
  // Google Books API 
  const [title, setTitle] = useState<string>('')
  // Google Books API 
  const [authors, setAuthors] = useState<string[]>([''])
  // 
  const [studentNo, setStudentNo] = useState<string>('')
  // 
  const [isCamOn, setIsCamOn] = useState<boolean>(false)
  
  //  ref
  const webcamRef = useRef<Webcam>(null);

  // 
  const videoConstraints = {
    width: 1920,
    height: 1080,
    facingMode: "environment"
  };

  // 
  const capture = useCallback(
    () => {
      const imageSrc = webcamRef.current?.getScreenshot();
      if( imageSrc != null){
        //  null image OCR 
        setImage(imageSrc)
        doOCR(imageSrc)
      }
    },
    [webcamRef]
  )

  // 
  const delImage = () =>{
    setImage(null)
  }

  // OCR 
  const worker = createWorker({
    logger: m => console.log(m),
  })

  // OCR 
  const doOCR = async (img: string) => {
    await worker.load();
    await worker.loadLanguage('eng');
    await worker.initialize('eng');
    const { data: { text } } = await worker.recognize(img);

    //  ocr 
    setOcr(text);

    //  ISBN 
    const result = text.match(/(I|1)(S|5|s)(B|8|5)N[0-9 -]{9,18}/)
    if(result){
      // ISBN  isbn 
      let resultString = result[0].toString()
      console.log(resultString)
      resultString = resultString.slice(4).replaceAll("-", "").replaceAll(" ", "")
      setIsbn(resultString)
    }
  }

  // 
  const toggleCam = () => {
    setIsCamOn(!isCamOn)
  }

  // isbn 
  // Google Books API 
  useEffect(() =>{
    // isbn  ISBN 
    if ((isbn.length===9) || (isbn.length===10) || (isbn.length===13)){ 
      const url = "https://www.googleapis.com/books/v1/volumes?q=isbn:" + isbn;
      // Google Books API 
      axios.get(url)
        .then((res: AxiosResponse<RES>) => {
          console.log(res)
          // response 
          if(res.data.totalItems > 0){
            console.log(res.data.items[0].volumeInfo.title)
            // 
            setTitle(res.data.items[0].volumeInfo.title)
            setAuthors(res.data.items[0].volumeInfo.authors)
          } else {
            // 
            setTitle("")
            setAuthors([""])
          }
        })
    }
  }, [isbn])

  // TextField  ISBN 
  const isbnOnChangeHandler = (e: React.ChangeEvent<HTMLInputElement>) =>{
    const _isbn = e.target.value
    setIsbn(_isbn)
  }

  return (
    <>
    {
      isCamOn ? (
        <>
        {
          image == null ? (
            <>
              <Box
                sx={{
                  width: "30em",
                  height: "30em",
                  border: "1px solid black",
                }}
              >
                <Webcam
                  audio={false}
                  ref={webcamRef}
                  forceScreenshotSourceSize
                  screenshotFormat="image/jpeg"
                  width="100%"
                  height="100%"
                  videoConstraints={videoConstraints}
                />
              </Box>
              <Button onClick={toggleCam} variant="contained"></Button>
              <Button onClick={capture} variant="contained"></Button>
            </>
          ) : (
            <>
              <Box
                sx={{
                  width: "30em",
                  height: "30em",
                  border: "1px solid black",
                }}
              >
                <img src={image} style={{ maxWidth: "100%", maxHeight:"100%"}} />
              </Box>
              <Button onClick={toggleCam} variant="contained"></Button>
              <Button onClick={delImage} variant="contained"></Button>
            </>
          )
        }
        </>
      ) : (
        <>
          <Button onClick={toggleCam} variant="contained">ISBN </Button>
        </>
      )
    }
    <p>{ocr}</p>

    <TextField
      value={isbn}
      label="ISBN"
      onChange={isbnOnChangeHandler}
    ></TextField>
    <TextField
      value={studentNo}
      label=""
    ></TextField>
    <Button variant="contained">
      
    </Button>
    <Typography>
      {
        title != "" &&
        authors.join(", ") + "" + title + ""
      }
    </Typography>
    </>
  );
}

export default App;
